import { get as _get, forEach as _forEach } from "lodash-es"
export const getUuid = () => {
    // http://www.ietf.org/rfc/rfc4122.txt
    var s = [];
    var hexDigits = "0123456789abcdef";
    for (var i = 0; i < 36; i++) {
        s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
    }
    s[14] = "4";  // bits 12-15 of the time_hi_and_version field to 0010
    s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);  // bits 6-7 of the clock_seq_hi_and_reserved to 01
    s[8] = s[13] = s[18] = s[23] = "-";

    var uuid = s.join("");
    return uuid;
}

export const getOAuthUrl = (authProperties) => {
    const { authorizationEndpoint, 
            authorizationQueryParams,
            authorizationHeaderParams,
            authorizationRequestBody,
            authorizationRequestBodyType,
            authorizationRequestMethod  } = authProperties
    console.log(authorizationQueryParams)
    const params = new URLSearchParams();
    _forEach(authorizationQueryParams, (value, key) => {
        params.set(key, value);
    });
    params.set('state', getUuid())
    const url = `${authorizationEndpoint}?${params.toString()}`;
    return url
  }
